@font-face {
    font-family: Oswald;
    src: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}

:root {
    font-size: 62.5%;
    --primary-font-colour: #f4f4f5;
    --secondary-font-colour: #d4d4d8;
    --primary-bg-colour: #52525b;
    --secondary-bg-colour: #27272a;
    --primary-trim-colour: #fcd34d;
    --secondary-trim-colour: #f97316;
}

body {
    box-sizing: border-box;
    margin: 0px;
    padding: 0px;
    width: 100vw;
    max-width: 100%;
    font-size: 1.5rem;
    background-color: var(--primary-bg-colour);
    overflow: scroll;
}

.header {
    background-color: var(--secondary-bg-colour);
    color: var(--primary-font-colour);
    font-family: GreatVibes, Cursive;
    width: 100%;
    box-shadow: 0px 1.5px 3px var(--primary-trim-colour);
    display: grid;
    grid-template-columns: 1.5fr max(50px, 5vw) 3.5fr;
    grid-template-rows: max(100px, 10vh);
    justify-items: center;
    align-items: center;
    position: sticky;
    top: 0px;
}

.header-title {
    font-size: 3rem;
    text-align: center;
    letter-spacing: 1.5px;
    text-shadow: 0.5px 0.5px var(--secondary-trim-colour);
}

.header-title > h1 {
    margin: 0px;
}

.header-title span {
    font-weight: 100;
}

.header-nav {
    width: 100%;
    grid-column: 3 / span 1;
}

.header-nav > ul {
    list-style: none;
    margin: 0px;
    padding: 0px;
    font-size: 4rem;
    letter-spacing: 3px;
    font-style: bold;
    display: flex;
    justify-content: space-around;
}

.header-nav li:hover {
    cursor: pointer;
    color: var(--secondary-font-colour);
    text-shadow: 0.5px 0.5px var(--primary-trim-colour);
}

.main-content-wrapper {
    padding: 5vh 15vw;
}

.about-us, .menu, .contact-us {
    box-shadow: 0px 0px 20px var(--secondary-trim-colour);
}

.about-us {
    display: flex;
}

.menu, .contact-us {
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    row-gap: 20px;
    column-gap: 30px;
}

.menu > h2, .contact-us > h2 {
    grid-column: 1 / span 2;
}

.contact-us > form {
    align-self: start;
    text-align: start;
    font-size: 2.5rem;
    color: var(--primary-font-colour);
    font-family: Oswald, sans-serif;
    display: grid;
    row-gap: 20px;
}

.form-row {
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.form-row > input, .form-row > textarea, .form-row > button {
    font-size: 2rem;
    font-family: Oswald, sans-serif;
    padding: 5px;
}

.form-row > button {
    width: 50%;
    padding: 10px 20px;
    align-self: center;
    letter-spacing: 2px;
    font-size: 2.5rem;
    box-shadow: 0px -0px 5px var(--primary-trim-colour);
}

.form-row > button:hover {
    background-color: var(--secondary-trim-colour);
    cursor: pointer;
}

.form-row > input:focus, .form-row > textarea:focus {
    outline: 2px solid var(--primary-trim-colour);
}

.menu > img, .contact-us > img {
    width: 100%;
    object-fit: cover;
}

.contact-us > img {
    max-height: 800px;
}

.menu> img {
    max-height: 400px;
}

.item-information > h3 {
    color: var(--primary-trim-colour);
    font-size: 2.5rem;
}

.item-information {
    align-self: start;
    height: 100%;
    padding: 0px 15px;
    background-color: var(--primary-bg-colour);
}

.image-wrapper, .information {
    flex-basis: 100%;
}

.about-us > .image-wrapper {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    background-color: var(--secondary-bg-colour);
}

.information, .menu, .contact-us {
    background-color: var(--secondary-bg-colour);
    text-align: center;
    padding: 20px;
}

.information > h2, .menu > h2, .contact-us > h2 {
    color: var(--primary-font-colour);
    font-family: GreatVibes, Cursive;
    letter-spacing: 3px;
    font-size: 3em;
    margin: 20px;
}

