    color: var(--secondary-font-colour);
    font-family: Oswald, sans-serif;
    font-size: 1.5em;
    letter-spacing: 1.5px;
}

.information > img {
    width: 90%;
    height: auto;
}

.footer {
    width: 100%;
    background-color: var(--secondary-bg-colour);
    height: max(5vh, 30px);
    box-shadow: 0px -0.5px 2px var(--primary-trim-colour);
}

.footer > ul {
    list-style: none;
    margin: 0px;
    padding: 0px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    height: 100%;
    color: var(--primary-font-colour);
    font-size: 2rem;
}

.footer a {
    color: var(--secondary-font-colour);
}

.footer a:hover {
    color: var(--primary-trim-colour);
}

.footer a:visited {
    color: var(--secondary-trim-colour);
}

.footer a:visited:hover {
    color: var(--primary-trim-colour);
}

.footer img {
    height: 25px;
}

.dev-details {
    display: flex;
    gap: 20px;
